:root {
  --color-black: hsl(0deg 0% 0%);
  --color-white: hsl(0deg 0% 100%);
  --neutral-000: hsl(0deg 0% 100%);
  --neutral-050: hsl(0deg 0% 98%);
  --neutral-100: hsl(0deg 0% 96%);
  --neutral-200: hsl(0deg 0% 90%);
  --neutral-300: hsl(0deg 0% 83%);
  --neutral-400: hsl(0deg 0% 64%);
  --neutral-500: hsl(0deg 0% 45%);
  --neutral-600: hsl(0deg 0% 32%);
  --neutral-700: hsl(0deg 0% 25%);
  --neutral-800: hsl(0deg 0% 15%);
  --neutral-900: hsl(0deg 0% 9%);
  --neutral-950: hsl(0deg 0% 4%);
  --emerald-500: hsl(160deg 84% 39%);
  --emerald-700: hsl(163deg 94% 24%);
  --blue-600: hsl(221deg 83% 53%);
  --purple-600: hsl(271deg 81% 56%);
  --red-600: hsl(0deg 72% 51%);
  --orange-600: hsl(21deg 90% 48%);
  --font-sans:
    Inter, system-ui, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu",
    "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  --font-serif: Georgia, "Times New Roman", Times, serif;
  --font-mono: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  --body-bg: var(--back-primary);
  --body-color: var(--front-primary);
  --font-base: var(--font-sans);
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
  --transition-fast: 0.125s ease;
  --transition-default: 0.25s ease;
  --transition-slow: 0.5s ease;
  --transition-slowest: 1s ease;
  --border-error: var(--red-600);
  --outline-color: var(--emerald-500);
  --lib-z-index-default: 1;
  --lib-z-index-noscript: 99;
  --container-padding: 16px;
  --container-max-width: 992px;
}
@media screen and (min-width: 768px) {
  :root {
    --container-padding: 32px;
  }
}
@media screen and (min-width: 1280px) {
  :root {
    --container-padding: 64px;
  }
}
@media (prefers-color-scheme: light) {
  :root {
    --front-primary: var(--neutral-900);
    --front-secondary: var(--neutral-700);
    --front-tertiary: var(--neutral-500);
    --front-invert: var(--neutral-100);
    --front-accent: var(--emerald-500);
    --back-primary: var(--neutral-100);
    --back-secondary: var(--neutral-200);
  }
}
@media (prefers-color-scheme: dark) {
  :root {
    --front-primary: var(--neutral-100);
    --front-secondary: var(--neutral-300);
    --front-tertiary: var(--neutral-500);
    --front-invert: var(--neutral-900);
    --front-accent: var(--emerald-500);
    --back-primary: var(--neutral-900);
    --back-secondary: var(--neutral-800);
  }
}

:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -moz-osx-font-smoothing: inherit;
  -webkit-font-smoothing: inherit;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: var(--body-color);
  font-weight: var(--font-weight-base);
  font-size: var(--font-size-base);
  font-family: var(--font-base);
  line-height: var(--line-height-base);
  background-color: var(--body-bg);
}

svg {
  display: block;
}

noscript {
  position: absolute;
  top: 8px;
  right: 8px;
  left: 8px;
  z-index: var(--lib-z-index-noscript);
  padding: 24px;
  text-align: center;
  background-color: var(--back-primary);
  border: 1px solid var(--border-error);
  border-radius: 8px;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

[hidden] {
  display: none;
}

@font-face {
  font-weight: 400;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("{{fonts}}Inter-Regular.woff2") format("woff2");
}
@font-face {
  font-weight: 500;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("{{fonts}}Inter-Medium.woff2") format("woff2");
}
@font-face {
  font-weight: 700;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("{{fonts}}Inter-Bold.woff2") format("woff2");
}
.container {
  width: min(100vw - var(--container-padding) * 2, var(--container-max-width));
  margin: 0 auto;
}

.footer {
  border-top: 1px solid var(--border-primary);
}
.footer__inner {
  padding: 16px 0;
}

.header {
  border-bottom: 1px solid var(--border-primary);
}
.header__inner {
  padding: 16px 0;
}

.map {
  outline: 1px solid red;
  outline-offset: -2px;
}

.page {
  display: flex;
  flex-direction: column;
  min-height: calc(var(--vh, 1vh) * 100);
}
.page__header {
  flex: 0 0 auto;
}
.page__main {
  flex: 1 0 auto;
}
.page__footer {
  flex: 0 0 auto;
}

